// vuexvue
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store= new Vuex.Store({
    // state data  state.$store.state.
    // 
  state: {
    type:'',
    currentCity:'',
    month1:new Date().getMonth()+1,
    day1:new Date().getDate(),
    month2:new Date().getMonth()+1,
    day2:new Date().getDate()+1,
  },
//   mutationsmethods state.$store.commit('',k,k)k
  mutations: {
      cmonth1(state,n){
        state.month1 = n;
      },
      cday1(state,n){
        state.day2 = n;
      },
      cmonth2(state,n){
        state.month2 = n;
      },
      cday2(state,n){
        state.day2 = n;
      },
      ccurrentCity(state,n){
        state.currentCity = n;
      }
  }
})
export default () =>store;


