<template>
    <div class="pro">
      <ul>
        <li v-for="(item,index) in list">
          <p>{{item.con}}</p>
        </li>
      </ul>
    </div>
  </template>
  <script>
  export default {
    data() {
      return {
        list: [
          {
            con: "",
          },
          {
            con: "",
          },
          {
            con: "",
          },
          {
            con: "",
          },
          {
            con: "",
          },
          {
            con: "",
          },
          {
            con: "",
          },
          {
            con: "/",
          },
          {
            con: "",
          },
        ],
      };
    },
  };
  </script>
  <style scoped>
  .pro {
    background: rgba(241, 243, 248, 0.541);
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }
  ul {
    width: 100%;
    background: white;
    overflow: hidden;
    margin-top: 4%;
  }
  ul > li {
    height: 49px;
      width: 100%;
      border: 1px solid rgb(212, 203, 203);
      line-height: 49px;
      border-left: none;
      border-right: none;
      font-size: 18px;
      color: #333;
  }
  ul > li > p {
    margin-left: 5%;
  }
  </style>


