                      <span class="three">></span>
                    </li>
                    <li >
                      <span class="one" @click="openPicker"></span>
                      <span class="two">{{month}}{{day}}</span> 
                        <mt-datetime-picker ref="picker" 
                              type="date" 
                              v-model="dataValue" 
                              confirmText='' 
                              cancelText = '' 
                              year-format = "{value} "
                              month-format = "{value} "
                              date-format = "{value} "
                              @confirm="handleConfirm"
                              :startDate="startDate">
                        </mt-datetime-picker>
                      <span class="week" @click="openPicker">{{week}}</span>
                      <span class="three" @click="openPicker">></span>
                    </li>
                    <li>
                      <span class="one"></span>
                      <span class="find">//</span>
                      <span class="three">></span>
                    </li>
                    <li>
                      <p></p>
                    </li>
                  </ul>
                </section>
                <section class="con" v-show='!flag'>
                  <ul>
                    <li @click='selectCity()'>
                      <span class="one"></span>
                      <span class="two">{{currentCity}}</span>
                      <span class="three">></span>
                    </li>
                        <li id="enterDay">
                          <span class="one" @click="openPicker"></span>
                          <b class="enter">{{month1}}{{day1}}<span @click="openPicker1">{{week1}}</span></b>
                          <mt-datetime-picker ref="picker1" 
