                                  v-model="dataValue1" 
                                  confirmText='' 
                                  cancelText = '' 
                                  year-format = "{value} "
                                  month-format = "{value} "
                                  date-format = "{value} "
                                  @confirm="handleConfirm1"
                                  :startDate="startDate">
                            </mt-datetime-picker>
                        </li>
                        <li id="leaveDay">
                          <b class="leave">{{month2}}{{day2}}<span @click="openPicker2">{{week2}}&nbsp;&nbsp;&nbsp;&nbsp;{{count}}</span></b>
                          <mt-datetime-picker ref="picker2" 
                                  type="date" 
                                  v-model="dataValue2" 
                                  confirmText='' 
                                  cancelText = '' 
                                  year-format = "{value} "
                                  month-format = "{value} "
                                  date-format = "{value} "
                                  @confirm="handleConfirm2"
                                  :startDate="startDate">
                            </mt-datetime-picker>
                          <span class="three">></span>
                    </li>
