        handleConfirm(value){
        this.year = value.getFullYear();
        this.month = value.getMonth()+1;
        this.day = value.getDate();
        
        var temp = value.getDay();
        if(temp == 0){
          this.week = "";
}else if(temp == 1){
  this.week = "";
}else if(temp == 2){
