        bottom: 13px;
        right:44px;
        font-size: 12px;
    }
    .comNum{
        position: absolute;
        bottom: 13px;
        right:0px;
        font-size: 12px;
    }
    .mp4{
        position: relative;
        width:92%;
        margin:3% 4%;
    }
    .vdTitle{
        font-family: PingFangSC-Medium,serif;
        position: absolute;
        top: 5px;
        left:16px;
        color:white;
        font-size: 16px;
        line-height: 26px;
    }
</style>


